<template>
  <div class="container">
    <el-container>
      <el-header>
        <h1>哈哈</h1>
        <el-menu
          :default-active="select"
          class="el-menu-demo"
          mode="horizontal"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          :router="true"
        >
          <el-menu-item index="index">首页0</el-menu-item>
          <el-menu-item index="index1">首页1</el-menu-item>
          <el-menu-item index="index2">首页2</el-menu-item>
          <el-menu-item index="index3">首页3</el-menu-item>
          <el-menu-item index="index4">首页4</el-menu-item>
          <el-menu-item index="index5">首页5</el-menu-item>
        </el-menu>
        <div class="header-right">
          <div class="title"><i class="el-icon-s-opportunity"></i>哈哈</div>
          <div class="date">
            <span>{{ time }}</span
            ><span>{{ week }}</span>
          </div>
          <!-- <el-button
            @click="drawer = true"
            type="primary"
            style="margin-left: 16px;"
          >
            点我打开
          </el-button> -->
          <el-dropdown trigger="click" @command="handleCommand">
            <span class="el-dropdown-link">
              <el-avatar
                size="medium"
                src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
              ></el-avatar
              ><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="login">注销</el-dropdown-item>
              <el-dropdown-item command="b">狮子头</el-dropdown-item>
              <el-dropdown-item command="c">螺蛳粉</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <router-view />
    </el-container>
    <el-drawer
      title="我是标题"
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose"
    >
      <span>我来啦!</span>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data () {
    return {
      select: null,
      drawer: false,
      direction: 'ltr',
      time: null,
      week: null
    }
  },
  mounted () {
    const hashArr = window.location.href.split('/')
    const selectArr = hashArr[hashArr.length - 1].split('-')
    this.select = selectArr[0]
    // console.log(this.$dayjs.unix(1360013296))
    // console.log(this.$dayjs(1631950353486).format('YYYY[年]MM[月]DD[日]'))
    // console.log(this.$dayjs(this.$dayjs()).valueOf())
    // console.log(this.$dayjs(this.$dayjs()).unix())
    this.getTime()
    this.getWeek()
  },
  methods: {
    getTime () {
      this.time = this.$dayjs().format('YYYY[年]MM[月]DD[日]')
    },
    getWeek () {
      switch (this.$dayjs().format('d')) {
        case '1':
          this.week = '星期一'
          break
        case '2':
          this.week = '星期二'
          break
        case '3':
          this.week = '星期三'
          break
        case '4':
          this.week = '星期四'
          break
        case '5':
          this.week = '星期五'
          break
        case '6':
          this.week = '星期六'
          break
        default:
          this.week = '星期日'
      }
    },
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    handleCommand (command) {
      if (command === 'login') {
        window.sessionStorage.clear()
        this.$router.push('/login')
      }
    }
  },
  watch: {
    $route: {
      handler: function (val, oldVal) {
        var path = val.path.split('/')
        this.select = path[path.length - 1].split('-')[0]
      },
      // 深度观察监听
      deep: true
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  width: 100%;
  height: 100%;
  .el-container {
    height: 100%;
    .el-header {
      background-color: #545c64;
      color: #333;
      line-height: 60px;
      padding: 0 10px;
      display: flex;
      h1 {
        margin: 0 20px 0 0;
      }
      .el-menu {
        height: 60px;
        overflow: hidden;
        flex: 1;
      }
      .header-right {
        display: flex;
        color: #fff;
        .title {
          margin-right: 20px;
        }
        .date {
          span {
            margin-right: 20px;
          }
        }
        .el-dropdown-link {
          cursor: pointer;
          .el-avatar {
            vertical-align: middle;
          }
          .el-icon-arrow-down {
            color: #fff;
          }
        }
      }
    }
  }
}
.container::-webkit-scrollbar {
  display: none;
}

